const puzzleButtonStyle = {
  'lineHeight': '9.5px',
  'letterSpacing': '1px',
  color: '#EEE',
}

function generateBoardAsciiArt(puzzle, boardStateStr) {
  if (!boardStateStr) {
    let boardAsciiArt = '';

    for (const char of puzzle.boardDataStr) {
      if (char === '#') {
        boardAsciiArt += '□';
      } else if (char === '\n') {
        boardAsciiArt += '\n';
      } else {
        boardAsciiArt += '■';
      }
    }

    return boardAsciiArt;
  }

  const boardState = parseBoardString(boardStateStr);
  const fakeCellGrid = createFakeCellGrid(boardState);
  const horizontalConveyors = getHorizontalConveyors(fakeCellGrid);
  const verticalConveyors = getVerticalConveyors(fakeCellGrid);

  const answerGrid = parseBoardString(puzzle.boardDataStr);
  const answerGridCells = createFakeCellGrid(answerGrid);
  const answerHorizontalConveyors = getHorizontalConveyors(answerGridCells);
  const answerVerticalConveyors = getVerticalConveyors(answerGridCells);

  for (let i = 0; i < horizontalConveyors.length; i++) {
    const conveyor = horizontalConveyors[i];
    const answerConveyor = answerHorizontalConveyors[i];

    let correct = true
    for (let j = 0; j < conveyor.length; j++) {
      const cell = conveyor[j];
      const answerCell = answerConveyor[j];

      if (cell.letter != answerCell.letter) {
        correct = false;
        break;
      }
    }

    conveyor.correct = correct
  }

  for (let i = 0; i < verticalConveyors.length; i++) {
    const conveyor = verticalConveyors[i];
    const answerConveyor = answerVerticalConveyors[i];

    let correct = true
    for (let j = 0; j < conveyor.length; j++) {
      const cell = conveyor[j];
      const answerCell = answerConveyor[j];

      if (cell.letter != answerCell.letter) {
        correct = false;
        break;
      }
    }

    conveyor.correct = correct
  }

  for (const conveyor of [...horizontalConveyors, ...verticalConveyors]) {
    if (conveyor.correct) {
      for (const cell of conveyor) {
        cell.correct = true
      }
    }
  }

  let boardAsciiArt = [];

  const collapsedGrid = fakeCellGrid.reduce((acc, row) => {
    return acc.concat([...row, {letter:'\n'}]);
  }, []);

  for (const cell of collapsedGrid) {
    const letter = cell.letter;
    if (letter === null) {
      boardAsciiArt.push(<span>□</span>);
    } else if (letter==='\n') {
      boardAsciiArt.push(<br/>);
    } else if (cell.correct) {
      boardAsciiArt.push(<span style={{color:'#8bd69e'}}>■</span>);
    } else {
      boardAsciiArt.push(<span>■</span>);
    }
  }

  return boardAsciiArt
}






const Main = () => {
  const [puzzle, setPuzzle] = useState(null);
  const [boardState, setBoardState] = useState(null);
  const [showConfirmBack, setShowConfirmBack] = useState(false);

  const [playJingle] = useSound(jingle);

  const sounds = { playJingle }

  const handleBackClick = () => {
    if (puzzle) {
      setShowConfirmBack(true);
    }
  };

  const confirmBack = (confirm) => {
    if (confirm) {
      setPuzzle(null); // Go back
    }
    setShowConfirmBack(false); // Close the confirmation dialog
  };

  const createPuzzleButton = (puzzle, i) => {
    const boardStateStr = localStorage.getItem(puzzle.id) || null;

    return <Button
      style={puzzleButtonStyle}
      key={i}
      onClick={() => setPuzzle(puzzle)}
    >
      <pre style={puzzleButtonStyle}>{generateBoardAsciiArt(puzzle, boardStateStr)}</pre>
    </Button>
  }

  const miniButtons = puzzles.minis.map(createPuzzleButton);
  const biggieButtons = puzzles.biggies.map(createPuzzleButton);

  if (puzzle) {
    const boardStateStr = localStorage.getItem(puzzle.id) || null;

    return (
      <div className="">
        {/* <IconButton style={backButtonStyle} onClick={() => setShowConfirmBack(true)}> */}
        <IconButton style={backButtonStyle} onClick={() => setPuzzle(null)}>
          <KeyboardBackspaceIcon />
        </IconButton>

        <div className="flex justify-center items-center h-screen">
          <Canvas app={app} puzzle={puzzle} sounds={sounds} boardStateStr={boardStateStr} setBoardState={setBoardState} {...config} />
        </div>
        <Dialog
          open={showConfirmBack}
          onClose={() => confirmBack(false)}
          aria-labelledby="alert-dialog-title"
        >
          <DialogTitle id="alert-dialog-title">
            {"Are you sure you want to go back?"}
          </DialogTitle>
          <DialogActions>
            <Button onClick={() => confirmBack(false)}>Cancel</Button>
            <Button onClick={() => confirmBack(true)} autoFocus>
              Confirm
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  } else {
    return (
      <div className="flex flex-col justify-center items-center" style={{ width: "100%" }}>
        <div style={{ maxWidth: '600px', padding: '15px' }}>
          <h1 className="text-4xl font-bold mb-4 text-center md:text-left" style={{ color: 'white', marginTop: "50px" }}>slidecross</h1>
          <h4 className="text-xs mb-4 text-center md:text-left" style={{ color: 'white' }}>a puzzle game by <a href="https://twitter.com/mateomartori" style={{ color: 'white' }}>matthew martori</a></h4>

          <h2 className="text-2xl text-center md:text-left" style={{ color: 'white', marginTop: "50px" }}>minis</h2>
          <div className="grid grid-cols-3 gap-4 md:grid-cols-8">
            {miniButtons}
          </div>

          <h2 className="text-2xl text-center md:text-left" style={{ color: 'white', marginTop: "50px" }}>biggies</h2>
          <div className="grid grid-cols-1 gap-2 md:grid-cols-3">{biggieButtons}</div>
        </div>
      </div>
    );
  }
};


